* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  padding: 15px 0;
  display: flex;
}

.field {
  border: 1px solid;
  border-radius: 15px;
  flex-direction: column;
  margin: 0 10px;
  display: flex;
  overflow: hidden;
  box-shadow: 10px 10px 8px 2px #0000004d;
}

.value {
  text-align: center;
  background-color: #b8c6e24d;
  padding: 0 15px;
  font-size: 50px;
}

.label {
  text-align: center;
  color: #fafafa;
  background-color: #444141;
}

